<template>
  <div class="main-content-wrapper">
    <div class="header">
      <img
        src="../assets/img/menu.png"
        alt="菜单"
        @click="openLeftSlider()"
        class="menu"
      >
      <span class="title"> 应急救援舆情</span>
      <img
        src="../assets/img/search.png"
        alt="菜单"
        @click="openRightSlider()"
        class="search"
      >
    </div>

    <div
      class="map"
      id="map"
    >
    </div>
    <div class="news-slider">
      <span class="news-left">
        <i class="icon iconfont icon-icon_on_the_left news-left-arrow"></i>
      </span>
      <ul
        class="news-box"
        id="news-box"
      >
      </ul>
      <span class="news-right">
        <i class="icon iconfont icon-icon_on_the_right news-right-arrow"></i>
      </span>
    </div>
    <div class="news-list">
      <div class="list-item">

      </div>
    </div>
    <div id="page">

    </div>
    <div
      class="tip"
      id="tip"
    ></div>
    <div
      class="loading-container"
      id="loadding"
    >
      <div class="spinner">
        <div class="spinner-container container1">
          <div class="circle1"></div>
          <div class="circle2"></div>
          <div class="circle3"></div>
          <div class="circle4"></div>
        </div>
        <div class="spinner-container container2">
          <div class="circle1"></div>
          <div class="circle2"></div>
          <div class="circle3"></div>
          <div class="circle4"></div>
        </div>
        <div class="spinner-container container3">
          <div class="circle1"></div>
          <div class="circle2"></div>
          <div class="circle3"></div>
          <div class="circle4"></div>
        </div>
      </div>
    </div>

  </div>
</template>
<script>
import util from "@/assets/js/util.js";

export default {
  data() {
    return {
      map: null,
      geoLayer: null,
      popUp: null,
      timer: null,
      tiptimer: null,
      popTimer: null,
      center: [116.38, 39.9],
      result: [], //mapnews
      newsFly: [], //地图新闻
      newsAll: [], //所有新闻
      swi: true,
      server: "http://122.137.242.15/dcms",
      getImgUrl: "", //实验地址
      allNewsUrl: "http://47.104.231.227:8080/yuqing/yuqing/getAllPage.do",
      len: 0,
      view: null,
      a_index: 0,
      flyTime: 3000,
      listenerKey: null,
      waitTime: 4000,
      hideTime: 2000,
      width: 0,
      sendData: {
        pageNo: 1,
        pageSize: 7,
        startTime: "",
        endTime: ""
      },
      totalPage: 0
    };
  },
  watch: {},
  mounted() {
    this.init();
    window["NEWS"] = {
      search: this.search
    };
  },
  computed: {},
  filters: {},
  components: {},
  methods: {
    openLeftSlider() {
      androidHandler.openLeftSlider();
    },
    openRightSlider() {
      androidHandler.openRightSlider();
    },

    init: function() {
      var that = this;
      that.initMap();
      that.bindEvent();
      new Promise((resolve, reject) => {
        that.getData(that.allNewsUrl, that.sendData, function(data) {
          resolve()
          that.setPages(data);
          that.getnewsHandle(data, true);
        });
      })
        .then(() => {
          that.startTime(); //初始化开始轮播
          window.onresize = function() {
            that.map && that.map.updateSize();
          };
        })
        .catch(error => {
          console.log(error);
        });
    },
    setPages: function(data, hasPage) {
      // console.log(hasPage);
      var that = this;
      if (
        data &&
        data.result &&
        data.result.total &&
        Object.prototype.toString.call(data.result.data) == "[object Array]" &&
        data.result.data.length > 0
      ) {
        var item = data.result;
        this.sendData.pageNo = item.pageNo;
        this.sendData.pageSize = item.pageSize;
        this.totalPage = item.totalPage;
        if (hasPage) {
          $("#page").show(500, function() {
            $("#page").pagination(
              "setPage",
              item.pageNo,
              item.totalPage ? item.totalPage : 1
            );
          });
          return;
        }
        $("#page").show(500, function() {
          $("#page").pagination({
            totalPage: item.totalPage ? item.totalPage : 1, //分页总数
            currentPage: item.pageNo ? item.pageNo : 1, //初始页数
            count: 3,
            isShow: true,
            homePageText: "首页",
            endPageText: "尾页",
            prevPageText: "<",
            nextPageText: ">",
            activeClass: "activP", //active 类样式定义
            callback: function(page) {
              that.sendData.pageNo = page;
              that.getData(that.allNewsUrl, that.sendData, function(data) {
                that.setPages(data, true);
                that.getnewsHandle(data, true);
              });
            }
          });
        });
      } else {
        $("#page").hide();
      }
    },
    getnewsHandle: function(data, changeFlynews) {
      if (
        data &&
        data.result &&
        data.result.data &&
        Object.prototype.toString.call(data.result.data) == "[object Array]"
      ) {
        var newsAll = data.result.data;
        var that = this;
        if (changeFlynews) {
          var newsFly = newsAll.filter(function(item) {
            if (item.lonlat) {
              return item;
            }
          });
          that.len = newsFly.length;
          if (that.len > 2) {
            that.newsFly = newsFly; //地图新闻
            var $newsList = $("#news-box");
            $newsList.empty();
            var strArr = [];
            for (var i = 0; i < that.len; i++) {
              var listN = newsFly[i];
              strArr.push("<li>" + listN.title + "</li>");
            }
            $newsList.append(strArr.join(""));
            that.changeListItem(0);
          }

          // $(".news-box").mCustomScrollbar({
          //     theme: "minimal-dark",
          //     axis: "x",
          //     autoHideScrollbar: false
          // });
        }
        that.newsAll = newsAll; //所有新闻
        that.creatNewCenter(that.newsAll);
        if (newsAll.length <= 0) {
          that.emptyText();
        }
      } else {
      }
    },
    emptyText: function() {
      var str = ' <h3 class="no-more text-center ">暂无数据...</h3>';
      $(".news-list").html(str);
    },
    bindEvent: function() {
      var that = this;
      var w = $("div.news-box li")
        .eq(0)
        .outerWidth();
      $(".news-slider").off();

      $(".news-list").off();
      $(".news-list").on("click", ".list-item", function() {
        var url = $(this).attr("data-id");
        if (url) {
          androidHandler.showDetail(url);
        }
      });
    },
    initMap: function() {
      this.view = new ol.View({
        center: ol.proj.fromLonLat([116.38, 39.9]),
        zoom: 9
      });
      var baseLayer = this.tile();
      //OSM();
      this.map = new ol.Map({
        layers: [baseLayer],
        controls: ol.control.defaults({
          attribution: false,
          zoom: false,
          dragAble: false
        }),
        loadTilesWhileAnimating: true,
        loadTilesWhileInteracting: false,
        target: "map",
        view: this.view
      });

      // this.geoLayer = this.grapihcLayer();
    },
    addNewsItems: function() {
      var that = this;
    },
    search: function(data) {
      if (typeof data == "string") {
        data = JSON.parse(data);
      }
      this.sendData.pageNo = 1; //搜索的时候no变成1；
      this.sendData.pageSizeNo = 7; //搜索的时候no变成7；
      var sendDate = Object.assign(this.sendData, data);
      var that = this;
      that.getData(that.allNewsUrl, that.sendData, function(data) {
        that.setPages(data, true);
        that.getnewsHandle(data, true);
      });
    },
    changeListItem: function(order) {
      var that = this;
      this.a_index = order;
      // console.log(this.a_index,this.len);
      if (that.swi) {
        that.swi = false;
        var item = that.newsFly[order];
        var langlat = item.lonlat;
        var coors = langlat.split(",");
        var coords = [parseFloat(coors[0].trim()), parseFloat(coors[1].trim())];
        var point = this.Point(coords);
        // console.log('point', point,typeof (point[0]),typeof (point[1]));
        //隐藏红点和overlayer

        $(".alertLayer-circle").hide();
        that.show();

        try {
          that.flyTo(point);
        } catch (error) {
          console.log(error);
        }
        this.popTimer && clearTimeout(that.popTimer);
        this.popTimer = setTimeout(function() {
          that.view.setCenter(point);
          that.view.setZoom(8);
          that.showPopUp(point, order, item);
          $(".alertLayer-circle").show();
        }, this.flyTime + 100);
      }
    },
    //圆形样式
    circleStyle: function() {
      var image = new ol.style.Circle({
        radius: 6,
        fill: new ol.style.Fill({
          color: "red"
        }),
        stroke: new ol.style.Stroke({ color: "red", width: 1 })
      });

      var style = new ol.style.Style({
        image: image
      });

      return style;
    }, //点闪烁样式
    flyTo: function(point) {
      //飞向
      var view = this.view;
      var duration = this.flyTime;

      function flyToPoint(view, location, done) {
        var zoom = view.getZoom();
        var parts = 2;
        var called = false;
        function callback(complete) {
          --parts;
          if (called) {
            return;
          }
          if (parts === 0 || !complete) {
            called = true;
            done(complete);
          }
        }
        view.animate(
          {
            center: location,
            duration: duration
          },
          callback
        );
        view.animate(
          {
            zoom: zoom - 5,
            duration: duration / 2
          },
          {
            zoom: zoom,
            duration: duration / 2
          },
          callback
        );
      }

      flyToPoint(view, point, function() {
        // view.setCenter(point);
        // view.setZoom(8);
      });
    },
    flash: function(feature) {
      var that = this;
      var duration = 15000;
      var start = new Date().getTime();

      function animate(event) {
        try {
          var vectorContext = event.vectorContext;
          var frameState = event.frameState;
          var flashGeom = feature.getGeometry().clone();
          var elapsed = frameState.time - start;
          var elapsedRatio = elapsed / duration;

          //radius will be 5 at start and 30 at end;
          var radius = ol.easing.easeOut(elapsedRatio) * 20 + 10;
          var opacity = ol.easing.easeOut(1 - elapsedRatio);

          var style = new ol.style.Style({
            image: new ol.style.Circle({
              radius: radius,
              snapToPixel: false,
              stroke: new ol.style.Stroke({
                color: "rgba(255, 0, 0, " + opacity + ")",
                width: 1.25 + opacity
              })
            })
          });
          vectorContext.setStyle(style);
          vectorContext.drawGeometry(flashGeom);
          if (elapsed > duration) {
            ol.Observable.unByKey(that.listenerKey);
            return;
          }
          that.map.render();
        } catch (error) {
          console.log(error);
        }
      }

      that.listenerKey = that.map.on("postcompose", animate);
    },
    stopFlash: function() {
      if (this.listenerKey) {
        ol.Observable.unByKey(this.listenerKey);
      }
    },
    //设置定时器
    setTimer: function() {
      var len = this.newsFly.length;
      if (this.a_index >= len - 1) {
        this.a_index = -1;
      }
      this.a_index++;
      len && this.changeListItem(this.a_index);
      // console.log('trigger',this.a_index);
      // $(as[this.a_index]).trigger('click');
    }, //开始定时
    startTime: function() {
      var _this = this;
      var interValTime = this.waitTime + this.flyTime + this.hideTime;
      this.timer && clearInterval(_this.timer);
      this.timer = setInterval(
        function() {
          _this.setTimer();
        }.bind(this),
        interValTime
      );
    }, //停止定时器
    stopTime: function() {
      if (this.timer) {
        clearInterval(this.timer);
      }
    },
    //添加图层
    addLayer: function(layer) {
      this.map.addLayer(layer);
    }, //清空graphicLayer
    clear: function() {
      this.geoLayer.getSource().clear(); //清除要素
      if (this.popUp) {
        //关闭popUp
        this.show();
      }
    }, //切换右侧列表信息
    toXY: function(coords) {
      //转换为平面坐标
      var point = ol.proj.fromLonLat(coords);
      return point;
    },
    //添加地物
    addGeo: function(geom) {
      var feature = this.Feature(geom);
      this.geoLayer.getSource().addFeature(feature);
    }, //网络地图的服务地址
    arcgisTile: function() {
      var url =
        "http://219.149.226.179:6080/arcgis/rest/services/jilin/jilin_gaode_map/MapServer";
      var layer = new ol.layer.Tile({
        // extent: [14074891,5430880,14103631,5453469],
        source: new ol.source.TileArcGISRest({
          url: url
        })
      });
      return layer;
    },
    chinaProvider: function(params) {
      var layers = {
        TianDiTu: {
          Normal: {
            Map:
              "http://t{0-7}.tianditu.cn/DataServer?T=vec_w&X={x}&Y={y}&L={z}",
            Annotion:
              "http://t{0-7}.tianditu.cn/DataServer?T=cva_w&X={x}&Y={y}&L={z}"
          },
          Satellite: {
            Map:
              "http://t{0-7}.tianditu.cn/DataServer?T=img_w&X={x}&Y={y}&L={z}",
            Annotion:
              "http://t{0-7}.tianditu.cn/DataServer?T=cia_w&X={x}&Y={y}&L={z}"
          },
          Terrain: {
            Map:
              "http://t{0-7}.tianditu.cn/DataServer?T=ter_w&X={x}&Y={y}&L={z}",
            Annotion:
              "http://t{0-7}.tianditu.cn/DataServer?T=cta_w&X={x}&Y={y}&L={z}"
          }
        },

        MapABC: {
          Normal: {
            Map: "http://emap{0-3}.mapabc.com/mapabc/maptile?&x={x}&y={y}&z={z}"
          }
        },

        GaoDe: {
          Normal: {
            Map:
              "http://webrd0{1-4}.is.autonavi.com/appmaptile?lang=zh_cn&size=1&scale=1&style=8&x={x}&y={y}&z={z}"
          },
          Satellite: {
            Map:
              "http://webst0{1-4}.is.autonavi.com/appmaptile?style=6&x={x}&y={y}&z={z}",
            Annotion:
              "http://webst0{1-4}.is.autonavi.com/appmaptile?style=8&x={x}&y={y}&z={z}"
          }
        },
        Google: {
          Normal: {
            Map:
              "http://mt{0-3}.google.cn/vt/lyrs=p@258000000&hl=zh-CN&gl=CN&src=app&x={x}&y={y}&z={z}&s=Ga"
          },
          Satellite: {
            Map:
              "http://mt{0-3}.google.cn/vt/lyrs=y@258000000&hl=zh-CN&gl=CN&src=app&x={x}&y={y}&z={z}&s=Ga"
          }
        }
      };
      var server = (params && params.server) || "Google";
      var source = (params && params.source) || "Normal";
      var type = (params && params.type) || "Map";
      return layers[server][source][type];
    },
    tile: function(params) {
      var url = this.chinaProvider(params);
      var layer = new ol.layer.Tile({
        source: new ol.source.XYZ({
          url: url
        })
      });
      return layer;
    },
    geoQ: function() {
      var url =
        "http://map.geoq.cn/ArcGIS/rest/services/ChinaOnlineStreetGray/MapServer";
      var layer = new ol.layer.Tile({
        //
        source: new ol.source.TileArcGISRest({
          url: url
        })
      });

      return layer;
    },
    //获取Feature要素
    Feature: function(geom) {
      var g = new ol.geom.Point(geom);
      var feature = new ol.Feature({ geometry: g });
      // var feature = new ol.Feature(geom);//原来的
      return feature;
    },
    //获取点几何
    Point: function(coords) {
      var point = ol.proj.fromLonLat([
        parseFloat(coords[0]),
        parseFloat(coords[1])
      ]);
      return point;
    }, //获取网络地图//矢量数据地图资源

    vectorSource: function(features) {
      var source = new ol.source.Vector({
        features: features || []
      });
      return source;
    },
    grapihcLayer: function() {
      //获取矢量图层
      var source = this.vectorSource();
      var that = this;
      source.on("addfeature", function(e) {
        that.flash(e.feature);
      });

      var layer = new ol.layer.Vector({
        source: source,
        style: this.circleStyle(),
        map: this.map
      });
      return layer;
    },
    showPopUp: function(point, order, item) {
      if (!this.popUp) {
        $("#popup").hide();
        var popUpElem = $(
          '<div id="popup" class="ol-popup">' +
            '<a href="#" id="popup-closer" class="ol-popup-closer"></a>' +
            '<div class="ol-popup-content clearfix">' +
            '<div id="popup-content" class="pop-title"></div>' +
            '<div id="popup-from" ></div>' +
            '<div id="popup-date"></div>' +
            "</div>" +
            '<div class="alertLayer-circle"><div class="alertLayer-center"></div></div>' +
            "</div>" +
            ""
        );

        popUpElem.appendTo(document.body);
        var container = document.getElementById("popup");
        var closer = document.getElementById("popup-closer");

        this.popUp = new ol.Overlay(
          /** @type {olx.OverlayOptions} */ ({
            element: container,
            autoPan: false,
            offset: [-113, 0],
            // autoPanAnimation: {
            //     duration: 5
            // },
            positioning: "center-center"
          })
        );
        this.map.addOverlay(this.popUp);

        var that = this;
        closer.onclick = function() {
          that.popUp.setPosition(undefined);
          closer.blur;
          return false;
        };
      }
      $("#popup-content").text(item.title);
      $("#popup-from").text(item.plateForm);

      var time = util.formatDate(new Date(item.pubtime), "yyyy-MM-dd");

      $("#popup-date").text(time);
      $("#popup").off();
      $("#popup").on("click", function() {
        androidHandler.showDetail(item.url);
        return false;
      });
      this.show(point);
      this.swi = true;
    },
    show: function(point) {
      //控制弹出框的显隐性
      var that = this;
      if (point) {
        this.popUp.setPosition(point);
        $(".ol-popup").fadeIn(500, "linear");
      } else {
        $(".ol-popup").fadeOut(this.hideTime, function() {
          that.popUp.setPosition(undefined);
        });
        return false;
      }
    },
    seeDetail: function(url) {
      androidHandler.showDetail(url);
    },
    creatNewCenter: function(data) {
      var arr = data || [],
        list = [];
      for (var k = 0; k < arr.length; k++) {
        var time = util.formatDate(new Date(arr[k].pubtime), "yyyy-MM-dd");
        var str =
          ' <div class="list-item" data-id="' +
          arr[k].url +
          '"><h3 class="list-title">' +
          arr[k].title +
          "</h3>" +
          '       <div class="new-info">' +
          '  <div class="col-8 align-left">' +
          '        <span class="news-from ">' +
          arr[k].plateForm +
          "</span>" +
          '           <span class="news-date ">' +
          arr[k].same +
          "</span>\n" +
          '   </div><div class="col-4 align-right">' +
          '           <span class="news-time pull-right">' +
          time +
          "</span>" +
          "      </div></div> </div>";
        list.push(str);
      }
      $(".news-list").html(list.join(""));
    },
    message: function(msg) {
      var self = document.getElementById("tip");
      self.innerHTML = msg;
      self.style.display = "block";
      this.tiptimer && clearTimeout(this.tiptimer);
      this.tiptimer = setTimeout(function() {
        self.style.display = "none";
      }, 2000);
    },
    getData: function(url, data, callback) {
      var that = this;
      $("#loadding").show();
      $.ajax({
        url: url,
        type: "POST",
        async: true,
        data: data,
        dataType: "json",
        timeout: 10000,
        success: function(data) {
          if (typeof callback == "function") {
            callback(data);
          }
        },
        error: function(XMLHttpRequest, textStatus, errorThrown) {
          that.message("后台忙碌，请稍后再试...");
          // console.log(XMLHttpRequest.status);
          // console.log(XMLHttpRequest.readyState);
          // console.log(textStatus);
        },
        complete: function(XHR, TS) {
          $("#loadding").hide();
        }
      });
    }
  }
};
</script>
<style lang='less'>
</style>
